.search-icon {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 16px;
  height: 16px;
  border: 3px solid;
  border-radius: 100%;
  margin-left: -4px;
  margin-top: -4px;
  transition: all 0.3s ease 0s; }
  .search-icon:after {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
    border-radius: 3px;
    width: 3px;
    height: 8px;
    background: currentColor;
    transform: rotate(-45deg);
    top: 10px;
    left: 12px; }

.close-icon {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 22px;
  height: 22px;
  border: 2px solid;
  background: transparent;
  border-radius: 40px;
  transition: all 0.3s ease 0s; }
  .close-icon::after, .close-icon::before {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
    width: 12px;
    height: 2px;
    background: currentColor;
    transform: rotate(45deg);
    border-radius: 5px;
    top: 8px;
    left: 3px; }
  .close-icon::after {
    transform: rotate(-45deg); }
